@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&family=Montserrat:wght@500&display=swap);
@import url(https://use.fontawesome.com/d0c316cf02.js);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.12.1/css/all.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  margin: 0;
}

.main {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  align-items: center;
  justify-content: center;
  background-color: skyblue;
}
.quote-box {
  background-color: white;
  width: 50%;
}
#tweet-quote {
  display: flex;
  margin-right: auto;
  margin-left: 10px;
  margin-bottom: 10px;
  flex-direction: column;
  justify-content: center;
  color: white;
  border: 1px solid white;
  padding: 7px 10px 7px 10px;
  border-radius: 4px;
  background-color: skyblue;
  font-size: 16px;
  cursor: default;
}
#quotation {
  font-size: 12px;
}
#quote {
  opacity: 0;
}
#new-quote {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: skyblue;
  color: white;
  border: 0;
  border-radius: 4px;
  padding: 0 10px 0 10px;
  margin-right: 10px;
  margin-bottom: 10px;
  letter-spacing: -1px;
  font-size: 16px;
}
#new-quote:hover, #tweet-quote:hover {
  background-color: #9dd7ef;
}

.fade {
  animation-name: fade-out-in;
  animation-duration: 2s;
  animation-iteration-count: 1;
}

.transform {  
  transition: all 2s ease;
}



@keyframes fade-out-in {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.credit {
  margin-top: 10px;
  text-align: center;
  font-family: Montserrat;
  font-size: 10px;
  color: white;

  /* make text unselectable */
  -webkit-user-select: none; /* Safari */ /* Firefox */
  -ms-user-select: none; /* IE10+/Edge */
  user-select: none; /* Standard */
}


* {
  opacity: 0;
}


